@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@326&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Otomanopee+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Overlock+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  transition: color 50ms linear;
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
  line-height: 1.5rem;
}
.grid{
  display: grid;
}
.flex {
  display: flex;
}
i {
  margin: 1rem;
  cursor: pointer;
  color:var(--color);
}
.fieldsegment {
  width: 100%;
  align-items: center;
  justify-content: center;
  -webkit-clip-path: polygon(65% 0, 100% 11%, 100% 100%, 0 100%, 0 9%);
          clip-path: polygon(65% 0, 100% 11%, 100% 100%, 0 100%, 0 9%);
  background: linear-gradient(90deg,rgb(27, 27, 75),rgb(35, 78, 78));
  color: white;
}
fieldset {
  width:70%;
  padding:5%;
  margin-top: 5rem;
  border-radius: 10px;
}

*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
}
canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 10vw;
    opacity: 0.6;
    z-index: 3;
}
header{
    position: relative;
    max-width: 100vw;
    height: 80vh;
    background-color: #abe9cd;
    background-image: linear-gradient(315deg, #464b49 0%, #273033 74%);
    display: grid;
    justify-items: center;
    overflow: hidden;
    color: white;
    -webkit-clip-path: polygon(100% 0, 100% 90%, 42% 100%, 0 90%, 0 0);
            clip-path: polygon(100% 0, 100% 90%, 42% 100%, 0 90%, 0 0);
}
nav{
    position: absolute;
    top: 0;
    width: 90% ;
    height: 6vh;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
ul{
    display: flex;
    list-style: none;
}
li{
    padding: 1rem;
}
.segment{
    width: 98vw;
    height: 65vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
.desc{
    position: relative;
    display: flex;
    width: 45%;
    align-items: center;
    justify-content: center;
}
.img {
    position: relative;
    height: 100%;
}
.img img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;
    height: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-40%);
            transform: translate(-50%,-40%);
    transition: -webkit-transform 500ms ease-out;
    transition: transform 500ms ease-out;
    transition: transform 500ms ease-out, -webkit-transform 500ms ease-out;
}
.desc:hover > .pic{
    -webkit-transform: scale(1.05) translate(-60%,-40%);
            transform: scale(1.05) translate(-60%,-40%);
}
.desc:hover > .svg{
    -webkit-transform: scale(1.05) translate(-50%,-40%);
            transform: scale(1.05) translate(-50%,-40%);
}
.segment2{
    max-width: 100vw;
    align-items: center;
    justify-content: center;
}
.caption{
    width: 60%;
    height: 6rem;
    color: black;
    background: white;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    box-shadow: 1px 1px 5px 2px rgba(0, 0, 0, 0.576);
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    z-index: 5;
}
@media screen and (max-width:900px){
    h1{
        font-size: x-large;
    }
    .segment {
        width: 90%;
        justify-content: space-between;
    }
}
@media (orientation: portrait) {
    .segment {
        width: 100vw;
        height: 60vh;
        display: grid;
        place-items: center;
    }
    .disc {
        position: relative;
        width: 100vw;
        height: 30vh;
    }
    .img{
        height: 30vh;
        width: 30vh;
        border-radius: 50%;
        border: 5px solid white;
        overflow: hidden;
    }
    .img img{
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%,-50%);
                transform: translate(-50%,-50%);
    }
    .desc:hover > .svg{
        -webkit-transform: scale(1.05) rotate(90deg,90deg,0deg);
                transform: scale(1.05) rotate(90deg,90deg,0deg);
    }
    .desc:hover > .pic{
        -webkit-transform: scale(1.05) translate(-50%,-50%);
                transform: scale(1.05) translate(-50%,-50%);
    }
}

.socialicons{
    -webkit-transform: scale(1.6);
            transform: scale(1.6);
}
.programicons{
    font-size: 1.5rem;
}
.socialpallete {
    margin: 0.5rem;
    transition: all 0.2s ease-in;
}
.socialpallete:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    border: 1px solid rgba(255, 255, 255, 0.349);
    border-width: 1px 2px 2px 1px;
    background: linear-gradient(120deg,rgba(255, 255, 255, 0.356),rgba(0, 0, 255, 0));
    border-radius: 10px;
    border-width: 0px 1px 1px 0px;
}
.cardspace {
    background: url(/static/media/shapes.202ae2cc.jpg);
    margin-top: 0.5rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.card {
    width: 10%;
    aspect-ratio: 1;
    margin: 2%;
    background: rgba(213, 212, 212, 0.205);
    display: grid;
    place-items: center;
    color: black;
    border-left: 1.5px solid rgba(0, 0, 0, 0.4);
    border-top: 1.5px solid rgba(0, 0, 0, 0.4);
    border-right: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow:1px 1px 4px rgba(124, 124, 124, 0.493);
    transition: 300ms ease-out;
}
.card:hover {
    border-radius: 50%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-backdrop-filter: blur(1px);
            backdrop-filter: blur(1px);
}
.cardicons {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
@media screen and (max-width:900px) {
    .cardicons {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
}
@media (orientation: portrait) {
    .card {
        width: 20%;
        margin: 1% 2% 1% 2%;
    }
}
